import { _decorator, Component, Node, Vec3 } from "cc";
const { ccclass, property } = _decorator;

@ccclass("RotateAroundComp")
export class RotateAroundComp extends Component {
  @property()
  private angleRotateSpeed: number = 100;

  private isRotate: boolean = false;

  init() {
    this.isRotate = false;
  }

  private rotate(dt) {
    let angle = this.node.eulerAngles;
    this.node.setRotationFromEuler(
      angle.x,
      angle.y,
      angle.z + this.angleRotateSpeed * dt
    );
  }

  update(deltaTime: number) {
    if (this.isRotate) this.rotate(deltaTime);
  }

  switRotateStatus() {
    this.isRotate = !this.isRotate;
  }
}
